
.active:hover{
		background-color: #1e89e0;
}
.active{
		background-color: #1e89e0;
}
li:hover{
		background-color: #000;
		cursor: pointer;
}
#left{
		display: inline-block;
    float: left;
    top: 60px;
    left: 0;
	  color:#fff;
		width: 200px;
		height: 695px;
		background-color: #202020;
}
.oneMenu{
		padding: 15px 5px;
		border-bottom: 0.1px solid #000;
}
.twoMenuContent li{
		padding: 12px 20px;
		border-bottom: 0.1px solid #000;
}
@media screen and (max-width:1200px){
#left{
  display: none;
}
}



